import React from 'react';

import Icon from '../icon';
import OutboundLink from '../outbound-link';

import styles from './styles.module.css';

const Contact = ({ title }) => (
  <div className={styles.contact}>
    <h1>{title || 'Me gustan los mensajes.'}</h1>
    <p className={styles.email}>
      Saluda y envía un email:<br />
      <a href="mailto:lmdemanuel@gmail.com?subject=Hola%20Luis!" className={styles.link}>lmdemanuel@gmail.com</a>
    </p>
    <div className={styles.lineBreak} />
    <ul className={styles.social}>
      <li>
        <OutboundLink to="https://github.com/lmdemanuel/">
          <Icon name="github" />
        </OutboundLink>
      </li>
      <li>
        <OutboundLink to="https://www.linkedin.com/in/lmdemanuel">
          <Icon name="linkedin" />
        </OutboundLink>
      </li>
      <li>
        <OutboundLink to="https://twitter.com/lmdemanuel/">
          <Icon name="twitter" />
        </OutboundLink>
      </li>
    </ul>
  </div>
);

export default Contact;



