import React from 'react';

import ContentPage from '../../components/content-page';
import Contact from '../../components/contact';
import Meta from '../../components/meta';
import Wrapper from '../../components/wrapper';

const ContactPage = ({ location }) => (
  <ContentPage>
    <Meta title="Contacto" description="Si tienes un próximo proyecto del que te gustaría hablar o estás buscando colaborar y construir algo especial, saluda y envía un correo electrónico." location={location} />
    <Wrapper>
      <Contact />
    </Wrapper>
  </ContentPage>
);

export default ContactPage;



